@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);

*{

  margin: 0px 0px;
  padding: 0px 0px;
  box-sizing: border-box;
  text-decoration: none;
  font-family: 'Mulish', sans-serif;
}

.navbar{
  background-color: #fff !important;
  z-index: 1 !important;
}

.navbar-brand{
  font-size: 1.9rem !important;
  color: #565387 !important;
  text-shadow: 1px 1px 20px #17dbff !important;
  color: rgb(255 110 8) !important;
}

.navbar a{
  font-size: 1.3rem !important;
  text-transform: capitalize !important;
  
}

.navbar a:hover{
  /* color: #3498db !important; */
  color: darkblue !important;
  text-shadow: 2px 2px 20px #50ff0e !important;
  
}

.menu_active{
  color: blue !important;
  font-weight: bold;
  border-bottom: 1px solid #565387;
}


#header {
 
  width: 100%;
  height: 80vh;
  margin-top: 50px;
  /* background: url('./images/img4.gif');
  background-size: 50%;
  background-position: left;
  background-repeat: no-repeat; */
}

#header h1{
  margin: 0px 0px 10px 0px;
  font-size: 48px;
  font-weight: 700;
  
}

#header .brand-name{
  color: blue;
  text-shadow: 2px 2px 20px #00ff3b;
}

#header h2{
  color: #2b7313;
}


.home-page{
  
  display: -webkit-flex;
  
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  
}

#header .btn-get-started{
  border: 2px solid #0479ff;
  padding: 10px 16px 10px 16px;
  text-decoration: none;
  border-radius: 100px;
  cursor: pointer;
  transition: ease-in-out 1s;  

}


#header .btn-get-started:hover{
  
  background: #0d6efd;
  color: white;


}


.header-img{
  text-align: right;
  -webkit-transform: scale3d(1.2, 1.2, 1.2);
          transform: scale3d(1.2, 1.2, 1.2);
}

#header .animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
  0%{
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100%{
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}

@keyframes up-down{
  0%{
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100%{
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}


.card{
  box-shadow: 2px 2px 12px 0px black !important;
  transition: -webkit-transform ease-in-out 2s;
  transition: transform ease-in-out 2s;
  transition: transform ease-in-out 2s, -webkit-transform ease-in-out 2s;
}

.card:hover{
  -webkit-transform: translateY(10px);
          transform: translateY(10px);
}

.contact-bg{
  background: url(/the-web-planet/static/media/contact.aad42919.gif);
  background-repeat: no-repeat;
  background-size: cover;
}



.btn-submit{
  width: 100%;
  padding: 12px;
}

.footerr{
  text-align: center;
}

/* Responsive Code */



@media (max-width : 991px){
  #header {
    height: 60vh;
    text-align: center;
    
  }

  #header h1{
    font-size: 36px;
  }

  #header .header-img{
    text-align: center;
  }

  #header .header-img img{
    width: 50%;


  }

  #header .home-desc{
    width: 100%;

  }


  .footerr{
    margin-top: 123px;
    text-align: center;

  }


}


@media (max-width: 768px){
  #header{
    margin-top: 20px;
  }
  #header h1{
    font-size: 28px;
  }
  #header h2{
    font-size: 18px;
    margin-bottom: 30px;
  }
  #header .header-img img{
    width: 70%;
  }
  .contact-Img{
    -webkit-flex-direction: column;
            flex-direction: column;

  }

}

@media (max-width: 434px){
  .container-fluid{
    display: -webkit-flex !important;
    display: flex !important;
    -webkit-justify-content: center !important;
            justify-content: center !important;
    -webkit-align-items: center !important;
            align-items: center !important;
  }

  .contact-Img img{
    width: 61%;

  }

}
